<template>
  <div class="field-opt pageSpace h100">
    <my-breadcrumb :extra="extraBreadcrumb"></my-breadcrumb>
    <div class="pageContent">
      <div class="optContent" style="margin:20px 0">
        <a-form-model ref="fieldForm" :model="fieldForm" :rules="fieldFormRule" layout="horizontal">
          <p class="partTitle"></p>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="cname"
                has-feedback
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-input v-model="fieldForm.cname" placeholder="" />
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="ename"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-input v-model="fieldForm.ename" placeholder="" />
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="type"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select v-model="fieldForm.type" placeholder="">
                  <a-select-option value="string"></a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="componentType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select
                  v-model="fieldForm.componentType"
                  placeholder=""
                  @change="componentTypeChange"
                >
                  <a-select-option value="input"></a-select-option>
                  <a-select-option value="select"></a-select-option>
                  <a-select-option value="m-select"></a-select-option>
                  <!-- <a-select-option value="textarea"></a-select-option> -->
                  <!-- <a-select-option value="num"></a-select-option> -->
                  <!-- <a-select-option value="time"></a-select-option> -->
                  <a-select-option value="day"></a-select-option>
                  <a-select-option value="daterange"></a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <!--<a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="searchType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-radio-group v-model="fieldForm.searchType">
                  <a-radio value="term"></a-radio>
                  <a-radio
                    value="fuzzy"
                    :disabled="fieldForm.componentType === 'date' || fieldForm.componentType === 'daterange'"
                  ></a-radio>
                </a-radio-group>
              </a-form-model-item>
            </a-col>-->

            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="searchType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.searchType"></a-switch>
              </a-form-model-item>
            </a-col>

            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="isSearch"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.isSearch"></a-switch>
              </a-form-model-item>
            </a-col>
            <!-- <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item label="" prop="isStatistics" :labelCol="{span:4}" :wrapperCol="{span:18}">
                <a-switch v-model="fieldForm.isStatistics"></a-switch>
              </a-form-model-item>
            </a-col>-->
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="isEnabled"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.isEnabled"></a-switch>
              </a-form-model-item>
            </a-col>
            <!-- <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item label="" prop="isWrite" :labelCol="{span:4}" :wrapperCol="{span:18}">
                <a-switch v-model="fieldForm.isWrite"></a-switch>
              </a-form-model-item>
            </a-col>-->
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="rules"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select v-model="fieldForm.rules" placeholder="">
                  <a-select-option value="no"></a-select-option>
                  <a-select-option value="ip">IP</a-select-option>
                  <a-select-option value="email"></a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="isMoreValue"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch
                  v-model="fieldForm.isMoreValue"
                  :disabled="fieldForm.componentType === 'm-select'||fieldForm.componentType === 'select'||fieldForm.componentType === 'num'||fieldForm.componentType === 'date'||fieldForm.componentType === 'daterange'"
                ></a-switch>
              </a-form-model-item>
            </a-col>
          </a-row>
          <p class="partTitle"></p>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="defaultValue"
                :labelCol="{span:2}"
                :wrapperCol="{span:22}"
              >
                <div v-if="fieldForm.componentType === 'date'">
                  <a-select v-model="fieldForm.defaultValue" placeholder="">
                    <a-select-option value=""></a-select-option>
                    <a-select-option value="systemTime"></a-select-option>
                  </a-select>
                </div>
                <div v-else-if="fieldForm.componentType === 'daterange'">
                  <a-select v-model="fieldForm.defaultValue" placeholder="">
                    <a-select-option value=""></a-select-option>
                    <a-select-option value="today"></a-select-option>
                    <a-select-option value="threeDays"></a-select-option>
                    <a-select-option value="sevenDays"></a-select-option>
                    <a-select-option value="thirtyDays"></a-select-option>
                    <a-select-option value="threeMonth"></a-select-option>
                    <a-select-option value="halfYear"></a-select-option>
                    <a-select-option value="year"></a-select-option>
                  </a-select>
                </div>
                <div v-else>
                  <a-input v-model="fieldForm.defaultValue" placeholder="" />
                </div>
              </a-form-model-item>
            </a-col>
          </a-row>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
              <a-form-model-item
                label=""
                prop="isFrontComponent"
                :labelCol="{span:2}"
                :wrapperCol="{span:22}"
              >
                <a-switch v-model="fieldForm.isFrontComponent"></a-switch>
              </a-form-model-item>
            </a-col>
          </a-row>
          <a-card
            class="bt"
            title=""
            v-if="fieldForm.componentType === 'select' || fieldForm.componentType === 'm-select'"
          >
            <a-row :gutter="[24,24]" style="margin:0">
              <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
                <a-form-model-item
                  label=""
                  prop="selectComponentDataType"
                  :labelCol="{span:2}"
                  :wrapperCol="{span:22}"
                >
                  <a-radio-group v-model="fieldForm.selectComponentDataType">
                    <a-radio :value="1"></a-radio>
                    <a-radio :value="2">api</a-radio>
                  </a-radio-group>
                </a-form-model-item>
              </a-col>
              <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
                <a-form-model-item
                  label=""
                  prop="options"
                  :labelCol="{span:2}"
                  :wrapperCol="{span:22}"
                >
                  <div v-if="fieldForm.selectComponentDataType === 1">
                    <div v-for="(value,index) of selectOptions.values" :key="index">
                      <a-input
                        v-model="selectOptions.values[index]"
                        placeholder="key"
                        style="width:40%;margin-right:1%"
                      />
                      <span style="color:#fff;">~</span>
                      <a-input
                        v-model="selectOptions.labels[index]"
                        placeholder="value"
                        style="width:40%;margin:0 1%"
                      />
                      <a-button
                        type="default"
                        shape="circle"
                        @click="delOption('selectOptions',index)"
                      >
                        <a-icon type="minus"></a-icon>
                      </a-button>
                    </div>
                    <a-button type="primary" @click="addOption('selectOptions')">
                      <a-icon type="plus"></a-icon>
                    </a-button>
                  </div>
                  <div v-else>
                    <a-select v-model="fieldForm.options" placeholder="">
                      <a-select-option :value="inter.name" v-for="inter of interfaceData" :key="inter.id">{{inter.name}}</a-select-option>
                    </a-select>
                  </div>
                </a-form-model-item>
              </a-col>
            </a-row>
          </a-card>
        </a-form-model>
        <a-row :gutter="[24,24]">
          <a-col :xxl="24" :xl="24" class="ar action">
            <a-button type="primary" @click="save">
              <a-icon type="save"></a-icon>
            </a-button>
            <a-button type="default" @click="back">
              <a-icon type="rollback"></a-icon>
            </a-button>
          </a-col>
        </a-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "field-opt",
  data() {
    let validateKeywords = (rule,value,callback) => {
      if(!value.trim()){
        callback(new Error(''));
      } else {
        if(!this.$fun.regularTop(this.fieldForm.ename)){
          callback(new Error(''));
        } else if(this.$fun.inKeywords(this.fieldForm.ename)){
          callback(new Error(''));
        } if(this.nodeFiledsEn.indexOf(this.fieldForm.ename)!=-1){
              callback(new Error(''));
          }else {
          callback();
        }
      }
    };
    let validateNodeFileds = (rule,value,callback) => {
      if(!value.trim()){
        callback(new Error(''));
      } else {
          if(this.nodeFileds.indexOf(this.fieldForm.cname)!=-1){
          callback(new Error(''));
        }  else {
          callback();
        }
      }
    };
    return {
      extraBreadcrumb: [
        "",
        this.$route.query.channelName,
        this.$route.params.type == "edit" ? "" : "",
      ],
      fieldForm: {
        cname: "", // 
        ename: "", // 
        componentType: "input", // 
        type: "string", // 
        // searchType: "term", //  fuzzy term
        searchType: true, //  fuzzy term
        isSearch: true, // 
        // isStatistics: true, // 
        isEnabled: true, // 
        // isWrite:false, // 
        isMoreValue: false, // 
        isFrontComponent: false, // 
        rules: "no", //   string
        options: "", //   string
        defaultValue: "", //  string
        selectComponentDataType: 1, // 1 api 2
      },
      fieldFormRule: {
        cname: [
            // { required: true, message: "", trigger: "blur" }
            { validator: validateNodeFileds, trigger: "blur" },
            ],
        ename: [
          // { required: true, message: "", trigger: "blur" },
          { validator: validateKeywords, trigger: "blur" },
        ],
        componentType: [
          { required: true, message: "", trigger: "change" },
        ],
      },
      selectOptions: {
        values: [],
        labels: [],
      },
      interfaceData:[],
      nodeFileds:[],
      nodeFiledsEn:[]
    };
  },
  methods: {
    // 
    addOption(option) {
      this[option].values.push("");
      this[option].labels.push("");
    },
    delOption(option, i) {
      this[option].values.splice(i, 1);
      this[option].labels.splice(i, 1);
    },
    // 
    componentTypeChange(type) {
      this.fieldForm.defaultValue = "";
      switch (type) {
        case "m-select":
          this.fieldForm.isMoreValue = true;
          break;
        case "select":
          this.fieldForm.isMoreValue = false;
          break;
        case "num":
          this.fieldForm.isMoreValue = false;
          break;
        case "date":
          this.fieldForm.isMoreValue = false;
          this.fieldForm.searchType = true;
          // this.fieldForm.searchType = "term";
          break;
        case "daterange":
          this.fieldForm.isMoreValue = false;
          this.fieldForm.searchType = true;
          // this.fieldForm.searchType = "term";
          break;
      }
    },
    save() {
      // 
      let optType = this.$route.params.type;
      // ctype
      let ctype = this.$route.query.ctype;
        this.fieldForm.searchType = this.fieldForm.searchType?"term":"fuzzy";
        // 
      this.$refs.fieldForm.validate((valid) => {
        if (valid) {
          //  
          let data = this.$fun.bolToInt(this.fieldForm);
          let finallybol = true;
          if (
            data.componentType === "select" ||
            data.componentType === "m-select"
          ) {
            // 
            if (data.selectComponentDataType === 1) {
              // 
              let bol = this.isEffectSelectOptions();
              finallybol = bol;
              if (bol) {
                //  
                data.options = JSON.stringify(this.setHandleOptionData());
              }
            } else {
              finallybol = data.options?true:false;
              if(!finallybol){
                this.$message.warning("");
              }
            }
          }
          if(!finallybol) {
            return
          }
          let tempArr = ["objectId","ruleGroupId","ruleId"];
          data["ownerType"] = ctype;
          data[tempArr[ctype]] = this.$route.query.cid;

          if(optType === 'add'){
            this.addField(data);
          } else {
            this.editField(data);
          }
        } else {
          this.$message.warning("");
          return false;
        }
      });
    },
    //  selectOptions 
    isEffectSelectOptions() {
      let finallyBol = true;
      let bol =
        this.selectOptions.values.every((item) => item.trim().length <= 0) ||
        this.selectOptions.labels.every((item) => item.trim().length <= 0);
      let bol2 =
        Array.from(new Set(this.selectOptions.values)).length <
        this.selectOptions.values.length;
      if (bol) {
        this.$message.warning(
          "keyvalue,"
        );
        finallyBol = false;
      } else if (bol2) {
        this.$message.warning("key");
        finallyBol = false;
      } else {
        finallyBol = true;
      }
      console.log(finallyBol)
      return finallyBol;
    },
    // 
    setHandleOptionData() {
      let arr = [];
      this.selectOptions.values.forEach((key, index) => {
        let obj = {};
        obj.value = key;
        obj.label = this.selectOptions.labels[index];
        arr.push(obj);
      });
      return arr;
    },
    // 
    addField(param){
      this.$API.addField(param).then(res => {
        if(res.code === 1000) {
          this.$message.success('');
          history.go(-1);
        } else {
          this.$message.warning(res.message);
          console.error(res.message);
        }
      })
    },
    // 
    editField(param){
      this.$API.editField(param).then(res => {
        if(res.code === 1000) {
          this.$message.success('');
          history.go(-1);
        } else {
          this.$message.warning('');
          console.error(res.message,res.data);
        }
      })
    },
    //  fieldId 
    getFieldByFieldId(fieldId){
      this.$API.searchFieldById(fieldId).then(res => {
        if(res.code === 1000) {
          let data = this.$fun.intToBol(res.data);
            data.searchType = data.searchType==="term"?true:false;
          if(data.options&&data.selectComponentDataType ===1){
            // options
            let temp = JSON.parse(data.options);
            this.selectOptions.values = temp.map(list => list.value);
            this.selectOptions.labels = temp.map(list => list.label);
          }
          Object.assign(this.fieldForm,data);
        } else {
          this.$message.warning('');
          console.error(res.message,res.data);
        }
      })
    },
     // 
      getDicData(){
        let id = this.$route.query.cid;
        let ownerType = this.$route.query.ctype;
        this.$API.searchDic({id,ownerType}).then(res => {
              if(res.code === 1000){
                  for(let item in res.data){
                      this.nodeFileds.push(res.data[item].cname);
                      this.nodeFiledsEn.push(res.data[item].ename);
                  }
              } else {
                  this.nodeFileds = [];
                  this.nodeFiledsEn = [];
              }
          })
      },
    // 
    getInterface(){
      this.$API.getInterface({from:1,size:999}).then(res => {
        if(res.code === 1000){
          this.interfaceData = res.data;
        } else {
          this.interfaceData = [];
        }
      })
    },
    back(){
      history.go(-1)
    }
  },
  created() {
    if(this.$route.params.type === 'edit'){
      this.getFieldByFieldId(this.$route.query.fieldId);
    }
    this.getInterface();
    this.getDicData();
  },
};
</script>
<style>
.field-opt .ant-form-item {
  min-height: 60px;
}
.field-opt .action button {
  margin: 10px 10px;
  width: 120px;
}
.field-opt .pageContent {
 overflow-x: hidden
}
</style>
